import Header from '../components/Header';
import Head from 'next/head';
import ProductsTable from '../components/ProductsTable';

import MockData from '../static/mock-data.json';


export default class Shop extends React.Component {

    componentWillMount(){
        this.setState({
            "products": MockData,
            "count": MockData.count,
            "inputValue": ""
        })
    }

    addProduct(){
        let data = this.state.products
        data.push({
            "id": this.state.count+1,
            "name": this.state.inputValue,
            "price": 300
        })
        console.log(data)
        this.setState({
            "products": data,
            "count": this.state.count+1
        });
    }

    updateInput(evt){
        let data = this.state.products
        this.setState({
            "products": data,
            "inputValue": evt.target.value
        });
    }

    render() {
        return (
        <div>
            <Head>
            <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css" integrity="sha384-Gn5384xqQ1aoWXA+058RXPxPg6fy4IWvTNh0E263XmFcJlSAwiGgFAW/dAiS6JXm" crossorigin="anonymous" />
            <script src="https://code.jquery.com/jquery-3.2.1.slim.min.js" integrity="sha384-KJ3o2DKtIkvYIK3UENzmM7KCkRr/rE9/Qpg6aAZGJwFDMVNA/GpGFF93hXpG5KkN" crossorigin="anonymous"></script>
            <script src="https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.12.9/umd/popper.min.js" integrity="sha384-ApNbgh9B+Y1QKtv3Rn7W3mgPxhU9K/ScQsAP7hUibX39j7fakFPskvXusvfa0b4Q" crossorigin="anonymous"></script>
            <script src="https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/js/bootstrap.min.js" integrity="sha384-JZR6Spejh4U02d8jOt6vLEHfe/JQGiRRSQQxSfFWpi1MquVdAyjUar5+76PVCmYl" crossorigin="anonymous"></script>
            <link rel="stylesheet" href="../static/general.css"></link>
            </Head>
            <Header />

            <div className="section container">
                <h2>Products in Stock</h2>
                <div style={{margin:"10px 0"}}> 
                    <input type="text" placeholder="Item Name" onChange={ evt => { this.updateInput(evt) }}/>
                    <button type="button" onClick={() => { this.addProduct() } }>Add Item</button>
                </div>
                <ProductsTable products={this.state.products}/>
            </div>

        </div>
        )
    }

}
